import { css } from '@emotion/css';

function PrimaryButton(props) {
  const { title, classes, onClickFunction, isDisabled } = props;

  return (
    <button
      className={`${css`
        color: white;
        background-color: var(--accent-color);
        border-style: none;
        border-radius: 200vw;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: box-shadow 150ms ease-in-out, transform 150ms ease;
        &:hover {
          transform: scale(1.05);
          box-shadow: 0 0 20px rgba(0, 163, 255, 0.3);
          transition: box-shadow 50ms ease-in-out, transform 50ms ease;
        }
        &:active {
          transition: box-shadow 0ms ease-in-out, transform 0ms ease;
          filter: brightness(1.1);
          transform: scale(1.03);
          box-shadow: 0 0 20px rgba(0, 163, 255, 0.4);
        }
        &:disabled {
          filter: brightness(1.08) saturate(80%);
          cursor: not-allowed;
        }
        @media screen and (max-width: 550px) {
          & p {
            font-size: 12px;
          }
        }
      `} ${classes}`}
      onClick={onClickFunction}
      disabled={isDisabled === undefined ? false : isDisabled}
    >
      <p
        className={css`
          font-size: 14px;
          transition: transform 75ms ease;
        `}
      >
        {title}
      </p>
    </button>
  );
}

export default PrimaryButton;
