import '../../../style/Platform/games/game_card.css';

import { useEffect, useState } from 'react';
import BigNumber from 'bignumber.js';
import SecondaryButton from '../../UI/SecondaryButton';
import fetchFirebase from '../../utils/fetchFirebase';

function GameCard(props) {
  const { title, description, image, demo, devs, color, method, gameLink } =
    props;
  const [aboutAppear, setAboutAppear] = useState(true);
  const [winnersAppear, setWinnersAppear] = useState(false);
  const [bookmarkPosition, setBookmarkPosition] = useState(0);
  const [gameInfo, setGamesInfo] = useState({});
  const [prevWinners, setPresWinners] = useState([]);
  const [winnersPlaceholder, setWinnersPlaceholder] = useState(true);
  let isSubscribed = true;

  useEffect(() => {
    const res = getGameInfo();
    if (res !== false) {
      res.then((info) => {
        if (isSubscribed) {
          setGamesInfo(info);
        }
      });
    }

    return () => {
      isSubscribed = false;
    };
  }, []);
  const walletFormating = (wallet) => {
    const start = wallet.slice(0, 5);
    const end = wallet.slice(-4);

    return start.concat('...', end);
  };
  const getWinners = async (title) => {
    let winnersArray;
    if (prevWinners.length === 0) {
      winnersArray = await fetchFirebase(title);
      setPresWinners(winnersArray);
      setWinnersPlaceholder(false);
    }
  };
  const getGameInfo = async () => {
    try {
      const info = await method.call();
      return info;
    } catch (e) {
      console.log(e.message ?? e);
      return false;
    }
  };
  const numberFormating = (num) => {
    const formated = BigNumber(num).dividedBy(BigNumber(10).pow(18)).toFixed();
    return new Intl.NumberFormat('de-DE').format(formated);
  };
  const handleLabelAbout = () => {
    setAboutAppear(true);
    setWinnersAppear(false);
    setBookmarkPosition(0);
  };
  const handleLabelWinners = () => {
    setAboutAppear(false);
    setWinnersAppear(true);
    setBookmarkPosition(122);
    getWinners(gameInfo.title);
  };

  return (
    <div className="platform_game_card">
      <div className="platform_game_card_bud font14 title">New</div>
      {/* FACE */}
      <div className="platform_game_card_face">
        <h3 className="platform_game_card_title title">{title}</h3>
        {/* TODO: uncomment when will add video */}
        {/* <h3 className="platform_game_card_title_ title">{title}</h3> */}
        <p className="platform_game_card_devs font14 title">by {devs}</p>
        <div
          className="game_card_bg_image"
          style={{
            background: `linear-gradient(0deg, ${color} 20%, rgba(0, 0, 0, 0) 65%)`,
          }}
        >
          <img src={image} />
        </div>
        <div className="platform_game_card_info">
          <div id="game_card_info_about_label">
            <svg
              style={{ left: bookmarkPosition }}
              width="144"
              height="38"
              viewBox="0 0 122 38"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M0 19C0 8.50659 8.50659 0 19 0H83C93.4934 0 102 8.50659 102 19C102 19 102 38 122 38H-20C0 38 0 19 0 19Z"
                fill="#00A3FF"
              />
            </svg>
            <span onClick={handleLabelAbout} className="title font14">
              About
            </span>
          </div>
          <div id="game_card_info_winners_label">
            <span onClick={handleLabelWinners} className="title font14">
              Winners
            </span>
          </div>
          <div
            className={`card_info_about ${
              aboutAppear ? 'card_info_about_apear' : 'card_info_about_disapear'
            }`}
          >
            <p className="title font14" style={{ opacity: 0.8 }}>
              {description}
            </p>
            <div className="platform_game_card_stats">
              {gameInfo.pool ? (
                <div
                  className="platform_game_card_stats_row"
                  style={{ marginTop: 0 }}
                >
                  <p>
                    <span className="title font16">Prize pool</span>
                  </p>
                  <p>
                    <span
                      className="title font16 fw700"
                      style={{ lineHeight: 1 }}
                    >
                      {numberFormating(gameInfo.pool)}
                    </span>{' '}
                    <span>
                      <svg
                        width="30"
                        height="24"
                        viewBox="0 0 30 24"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M21.7781 10.9999C19.7781 10.5999 15.6114 13.4999 13.7781 14.9999C11.9448 13.4999 7.77811 10.5998 5.77811 10.9998C4.77811 16 9.27811 22.5 9.27811 22.5C-4.72189 13 1.27811 0 1.27811 0C7.27811 1 13.7781 9 13.7781 9C13.7781 9 20.2781 1 26.2781 0C26.2781 0 29.2781 4 27.7781 10C27.7781 10 28.7781 10 29.7781 9C29.7781 9 28.7781 17.5 19.2781 24C19.2781 24 20.7781 22.5 23.2781 15.5C23.2781 15.5 22.7781 16 21.2781 16.5C21.2781 16.5 22.2781 13.5 21.7781 10.9999Z"
                          fill="white"
                        />
                      </svg>
                    </span>
                  </p>
                </div>
              ) : null}
              {gameInfo.countPlayers ? (
                <div className="platform_game_card_stats_row">
                  <p>
                    <span className="title font16">Now playing</span>
                  </p>
                  <p>
                    <span
                      className="title font16 fw700"
                      style={{ lineHeight: 1 }}
                    >
                      {gameInfo.countPlayers}
                    </span>
                  </p>
                </div>
              ) : null}
            </div>
            {gameLink ? (
              <a href={gameLink} target="_blank" rel="noreferrer">
                <SecondaryButton classes={'card_info_ablout_button'}>
                  <span className="title font14">
                    Play
                    {/* {gameInfo.entry && gameInfo.entry > 0
                  ? numberFormating(gameInfo.entry)
                  : ''} */}
                  </span>
                  <span>
                    <svg
                      width="30"
                      height="24"
                      viewBox="0 0 30 24"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M21.7781 10.9999C19.7781 10.5999 15.6114 13.4999 13.7781 14.9999C11.9448 13.4999 7.77811 10.5998 5.77811 10.9998C4.77811 16 9.27811 22.5 9.27811 22.5C-4.72189 13 1.27811 0 1.27811 0C7.27811 1 13.7781 9 13.7781 9C13.7781 9 20.2781 1 26.2781 0C26.2781 0 29.2781 4 27.7781 10C27.7781 10 28.7781 10 29.7781 9C29.7781 9 28.7781 17.5 19.2781 24C19.2781 24 20.7781 22.5 23.2781 15.5C23.2781 15.5 22.7781 16 21.2781 16.5C21.2781 16.5 22.2781 13.5 21.7781 10.9999Z"
                        fill="#00A3FF"
                      />
                    </svg>
                  </span>
                </SecondaryButton>
              </a>
            ) : (
              <a>
                <SecondaryButton classes={'card_info_ablout_button'}>
                  <span className="title font14">
                    Play
                    {/* {gameInfo.entry && gameInfo.entry > 0
                  ? numberFormating(gameInfo.entry)
                  : ''} */}
                  </span>
                  <span>
                    <svg
                      width="30"
                      height="24"
                      viewBox="0 0 30 24"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M21.7781 10.9999C19.7781 10.5999 15.6114 13.4999 13.7781 14.9999C11.9448 13.4999 7.77811 10.5998 5.77811 10.9998C4.77811 16 9.27811 22.5 9.27811 22.5C-4.72189 13 1.27811 0 1.27811 0C7.27811 1 13.7781 9 13.7781 9C13.7781 9 20.2781 1 26.2781 0C26.2781 0 29.2781 4 27.7781 10C27.7781 10 28.7781 10 29.7781 9C29.7781 9 28.7781 17.5 19.2781 24C19.2781 24 20.7781 22.5 23.2781 15.5C23.2781 15.5 22.7781 16 21.2781 16.5C21.2781 16.5 22.2781 13.5 21.7781 10.9999Z"
                        fill="#00A3FF"
                      />
                    </svg>
                  </span>
                </SecondaryButton>
              </a>
            )}
          </div>
          <div
            className={`card_info_winners ${
              winnersAppear
                ? 'card_info_winners_apear'
                : 'card_info_winners_disapear'
            }`}
          >
            <p
              className="title font16"
              style={{ textAlign: 'center', marginBottom: 10, opacity: 0.75 }}
            >
              Previous day winners
            </p>
            {prevWinners.length !== 0 &&
              prevWinners.map((item, index) => {
                return (
                  <div key={index} className="card_info_winners_row">
                    <span className="font14 title">{index + 1}</span>
                    <span className="font14 text">
                      {walletFormating(item[1])}
                    </span>
                    <span className="font14 title">{item[0]}</span>
                  </div>
                );
              })}
            {!winnersPlaceholder && prevWinners.length === 0 && (
              <p
                className="title font14 fw500"
                style={{ textAlign: 'center', marginTop: 20, opacity: 0.5 }}
              >
                List is empty for now...
              </p>
            )}
            <div
              className="card_info_winners_row row_placeholder"
              style={{
                display: winnersPlaceholder ? 'block' : 'none',
              }}
            ></div>
            <div
              className="card_info_winners_row row_placeholder"
              style={{
                display: winnersPlaceholder ? 'block' : 'none',
              }}
            ></div>
          </div>
          {/* </form> */}
        </div>
      </div>
    </div>
  );
}

export default GameCard;
