import '../style/Presale1/presale.css';

import { useEffect, useState } from 'react';
import { AppContext } from '../Components/App';
import BigNumber from 'bignumber.js';

import Header from '../Components/Presale/Header';
import Hero from '../Components/Presale/Hero';
import Main from '../Components/Presale/Main';
import LoaderScreen from '../Components/UI/LoaderScreen';

function Presale(props) {
  const CONTRACT_ADDRESS = '0x0fCB8F5d8e5D0F818DdAbA5cC06d4c280Df4a6DB';
  const { onConnect, onDisconnect, addNetwork, web3Account, address } = props;

  // const [address, setAddress] = useState(false);
  const [buyBtnDisadled, setBuyBtnDisadled] = useState(false);
  const [claimBtnDisadled, setClaimBtnDisadled] = useState(false);

  const [contractInstance, setContractInstance] = useState(null);
  const [saleStarted, setSaleStarted] = useState(false);
  const [saleFinalized, setSaleFinalized] = useState(false);

  const [isWhitelistEnabled, setIsWhitelistEnabled] = useState(false);
  const [isAccountWhitelisted, setIsAccountWhitelisted] = useState(false);
  const [availableToClaim, setAvailableToClaim] = useState(0);
  const [deposited, setDeposited] = useState(0);
  const [totalDeposited, setTotalDeposited] = useState(0);

  const [timeForLoad, setTimeForLoad] = useState(true);

  const HARDCAP = 200;
  const MIN_BUY = 0.1;
  const MAX_BUY = 2;

  const fetchContractData = async (contract) => {
    if (!contract) {
      return;
    }
    // Total
    const total = await contract.methods.receivedAmount().call();
    setTotalDeposited(
      BigNumber(total).dividedBy(BigNumber(10).pow(18)).toFixed()
    );
    // Whitelist enabled ?
    const whitelistEnable = await contract.methods.whiteListEnabled().call();
    setIsWhitelistEnabled(whitelistEnable);
    // Started ?
    const started = contract.methods.saleStarted().call();
    started.then((res) => {
      setSaleStarted(res);
    });
    // Finalized ?
    const finalized = contract.methods.saleFinilized().call();
    finalized.then((res) => {
      setSaleFinalized(res);
    });
  };
  const fetchUserData = async (contract) => {
    const accounts = await web3Account.eth.getAccounts();
    const account = accounts[0];
    // Gate Expression
    if (!account) {
      return;
    }
    // Curent account whitelisted ?
    const wled = await contract.methods.whitelistedAddress(account).call();
    setIsAccountWhitelisted(wled);

    // How much deposited ?
    const deposit = await contract.methods.depositedFrom(account).call();

    setDeposited(BigNumber(deposit).dividedBy(BigNumber(10).pow(18)).toFixed());
    // Available to claim ?
    const claimed = await contract.methods.availableToClaim(account).call();
    setAvailableToClaim(claimed);
  };
  const buy = async (amount) => {
    setBuyBtnDisadled(true);
    try {
      const formAmount = amount.replace(',', '.');
      const value = Number(web3Account.utils.toWei(formAmount)).toString(16);
      const encoded = await contractInstance.methods.deposit().encodeABI();

      const providerType = await window.localStorage
        .WEB3_CONNECT_CACHED_PROVIDER;

      if (providerType === '"injected"') {
        if (window.ethereum) {
          window.ethereum
            .request({
              method: 'eth_sendTransaction',
              params: [
                {
                  from: address,
                  to: CONTRACT_ADDRESS,
                  value,
                  gas: '85000',
                  data: encoded,
                },
              ],
            })
            .then((txHash) => {
              const interval = setInterval(async () => {
                const receipt = await web3Account.eth.getTransactionReceipt(
                  txHash
                );
                if (receipt) {
                  setBuyBtnDisadled(false);
                  clearInterval(interval);
                  document.location.reload();
                }
              }, 1000);
            })
            .catch((e) => {
              setBuyBtnDisadled(false);
              console.log('Deposit error!', e.message ?? e);
            });
        }
      } else {
        if (window.web3) {
          const valueInt = Number(web3Account.utils.toWei(formAmount));

          web3Account.eth.sendTransaction(
            {
              from: address,
              to: CONTRACT_ADDRESS,
              value: valueInt,
              gas: '85000',
              data: encoded,
            },
            (error, result) => {
              if (error) {
                setBuyBtnDisadled(false);
                console.log(
                  'Send Transaction: ',
                  error.message,
                  error.code ?? ' '
                );
              } else {
                const interval = setInterval(async () => {
                  const receipt = await web3Account.eth.getTransactionReceipt(
                    result
                  );
                  if (receipt) {
                    setBuyBtnDisadled(false);
                    clearInterval(interval);
                    document.location.reload();
                  }
                }, 1000);
              }
            }
          );
        }
      }
    } catch (e) {
      setBuyBtnDisadled(false);
      console.log('Deposit:', e.message ?? e);
    }
  };
  const claim = async () => {
    try {
      setClaimBtnDisadled(true);
      const encoded = contractInstance.methods.reclaimTokens().encodeABI();

      const providerType = await window.localStorage
        .WEB3_CONNECT_CACHED_PROVIDER;

      if (providerType === '"injected"')
        if (window.ethereum) {
          window.ethereum
            .request({
              method: 'eth_sendTransaction',
              params: [
                {
                  from: address,
                  to: CONTRACT_ADDRESS,
                  gas: '85000',
                  data: encoded,
                },
              ],
            })
            .then((txHash) => {
              const interval = setInterval(async () => {
                const receipt = await web3Account.eth.getTransactionReceipt(
                  txHash
                );
                if (receipt) {
                  setClaimBtnDisadled(false);
                  clearInterval(interval);
                  document.location.reload();
                }
              }, 1000);
            })
            .catch((e) => console.log('Claim error!', e.message ?? e));
        }
      if (providerType === '"walletconnect"' || providerType === '"torus"') {
        if (window.web3) {
          web3Account.eth.sendTransaction(
            {
              from: address,
              to: CONTRACT_ADDRESS,
              gas: '85000',
              data: encoded,
            },
            (error, result) => {
              if (error) console.log('Send Transaction: ', error);
              else {
                const interval = setInterval(async () => {
                  const receipt = await web3Account.eth.getTransactionReceipt(
                    result
                  );
                  if (receipt) {
                    clearInterval(interval);
                    document.location.reload();
                  }
                }, 1000);
              }
            }
          );
        }
      }
    } catch (e) {
      console.log(e.message ?? e);
    }
  };
  const claimDisabled = () => {
    if (address === '') {
      return true;
    }

    if (claimBtnDisadled) {
      return true;
    }

    if (!saleFinalized) {
      return true;
    } else {
      if (deposited > 0) {
        if (availableToClaim > 0) {
          return false;
        } else {
          return true;
        }
      } else {
        return true;
      }
    }
  };
  const buyDisabled = () => {
    if (address === '') {
      return true;
    }

    if (buyBtnDisadled) {
      return true;
    }

    if (!saleStarted) {
      return true;
    } else {
      if (isWhitelistEnabled && !isAccountWhitelisted) {
        return true;
      }
      if (deposited > 1.9) {
        return true;
      }
      return false;
    }
  };
  const setContract = async () => {
    // Dinamic Imports
    const { default: getContract } = await import(
      '../Components/utils/contractConnect'
    );
    const getAbi = await import('../Components/data/presaleAbi');

    // Setting Contract Instance
    const contract = await getContract(getAbi.default(), CONTRACT_ADDRESS, 56);
    setContractInstance(contract);
  };

  // ** Fetch Contract Data
  useEffect(() => {
    if (contractInstance) {
      fetchContractData(contractInstance);
    }
  }, [contractInstance]);
  // ** Fetch User Data
  useEffect(() => {
    if (web3Account && contractInstance && address) {
      console.log(address);
      fetchUserData(contractInstance);
    }
  }, [contractInstance, web3Account, address]);

  // ** Initial settings
  useEffect(() => {
    // Set Network
    addNetwork(56);

    // Loader Countdown
    setTimeout(() => {
      setTimeForLoad(false);
    }, 1500);

    // Contract Instance creation
    try {
      setContract();
    } catch (e) {
      console.log('Contract instance error: ', e.message ?? e);
    }
  }, []);

  return (
    <div className="presale">
      <LoaderScreen loading={false} timeForLoad={timeForLoad} />
      <div className="presale_bg"></div>
      <Header
        onConnect={onConnect}
        onDisconnect={onDisconnect}
        address={address}
      />
      <Hero
        address={address}
        claim={claim}
        claimDisabled={claimDisabled}
        contractInstance={contractInstance !== null && contractInstance}
        saleFinalized={saleFinalized}
        deposited={deposited}
      />
      {!timeForLoad && (
        <Main
          address={address}
          buy={buy}
          buyDisabled={buyDisabled}
          saleStarted={saleStarted}
          saleFinalized={saleFinalized}
          isWhitelistEnabled={isWhitelistEnabled}
          isAccountWhitelisted={isAccountWhitelisted}
          availableToClaim={availableToClaim}
          deposited={deposited}
          totalDeposited={totalDeposited}
          HARDCAP={HARDCAP}
          MIN_BUY={MIN_BUY}
          MAX_BUY={MAX_BUY}
        />
      )}
    </div>
  );
}

const PresaleContainer = (props) => {
  return (
    <AppContext.Consumer>
      {(context) => (
        <Presale
          onConnect={context.onConnect}
          onDisconnect={context.onDisconnect}
          web3Account={context.web3Account}
          address={context.address}
          {...props}
        />
      )}
    </AppContext.Consumer>
  );
};

export default PresaleContainer;
