import '../style/main.css';

// ** React
import React from 'react';
import { useState, useEffect } from 'react';

// ** Crypto
import Web3 from 'web3';
import Web3Modal from 'web3modal';
import WalletConnectProvider from '@walletconnect/web3-provider';
import Torus from '@toruslabs/torus-embed';

// ** Navigation
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import ScrollToTop from './utils/ScrollToTop';

// **Pages
import Home from '../Pages/Home';
import Presale from '../Pages/Presale';
import Platform from '../Pages/Platform';

export const AppContext = React.createContext();

function App() {
  const [web3Account, setWeb3Account] = useState(null);
  const [address, setAddress] = useState('');
  const [web3modal, setWeb3modal] = useState(null);

  const addNetwork = async (id) => {
    try {
      if (window.ethereum) {
        let networkData;
        switch (id) {
          case 97:
            networkData = [
              {
                chainId: '0x61',
                chainName: 'BSC TESTNET',
                rpcUrls: ['https://data-seed-prebsc-1-s1.binance.org:8545'],
                nativeCurrency: {
                  name: 'BINANCE COIN',
                  symbol: 'BNB test',
                  decimals: 18,
                },
                blockExplorerUrls: ['https://testnet.bscscan.com/'],
              },
            ];
            break;
          case 56:
            networkData = [
              {
                chainId: '0x38',
                chainName: 'Smart Chain',
                rpcUrls: ['https://bsc-dataseed.binance.org/'],
                nativeCurrency: {
                  name: 'BINANCE COIN',
                  symbol: 'BNB',
                  decimals: 18,
                },
                blockExplorerUrls: ['https://bscscan.com/'],
              },
            ];
            break;
          default:
            break;
        }

        return window.ethereum.request({
          method: 'wallet_addEthereumChain',
          params: networkData,
        });
      } else {
        console.log('no etereum');
      }
    } catch (e) {
      console.log(e);
    }
  };

  useEffect(() => {
    if (window.location.pathname !== '/') {
      if (window.localStorage.WEB3_CONNECT_CACHED_PROVIDER) {
        onConnect();
      }
    }
    callAPI();
  }, []);

  const providerOptions = {
    walletconnect: {
      package: WalletConnectProvider,
      options: {
        infuraId: '0273b4aa39fd49d781c10caa9c0d64dc',
        rpc: {
          97: 'https://data-seed-prebsc-1-s1.binance.org:8545/',
          56: 'https://bsc-dataseed1.binance.org',
        },
      },
    },
    torus: {
      package: Torus,
      networkParams: {
        host: 'https://bsc-dataseed1.binance.org',
        networkName: 'Binance Smart Chain',
        chainId: '56',
        blockExplorer: 'https://bscscan.com/',
        ticker: 'BNB',
        tickerName: 'BNB',
      },
    },
  };

  const onConnect = async () => {
    const web3 = await connection();
    window.web3 = web3;
    if (web3) {
      const accounts = await web3.eth.getAccounts();
      setAddress(accounts[0]);
      setWeb3Account(web3);
    }
  };

  const connection = async () => {
    let web3;
    try {
      const params = {
        cacheProvider: false,
        disableInjectedProvider: false,
        providerOptions,
      };
      const web3modal = new Web3Modal(params);
      setWeb3modal(web3modal);
      const provider = await web3modal.connect();

      web3 = new Web3(provider);
      return web3;
    } catch (e) {
      console.log('Closed modal:', e);
      return false;
    }
  };

  const onDisconnect = async () => {
    delete window.localStorage.WEB3_CONNECT_CACHED_PROVIDER;
    if (window.localStorage.walletconnect) {
      delete window.localStorage.walletconnect;
    }
    web3modal.clearCachedProvider();
    setAddress('');
  };

  const LINKS = {
    twitter: 'https://twitter.com/manarium_gg',
    telegram: 'https://t.me/Manarium_Chat',
    instagram: '',
    whitepaper:
      'https://drive.google.com/file/d/1GEOCmNJOWr64d6yhmqIt_19c8UdlNk96/view',
    smartContract: '',
    medium: 'https://medium.com/@manarium.news',
  };

  const callAPI = () => {
    fetch('/api')
      .then((res) => res.json())
      .then((res) => {
        console.log(JSON.stringify(res));
      });
  };

  return (
    <AppContext.Provider
      value={{ LINKS, onConnect, web3Account, onDisconnect, address }}
    >
      <Router>
        <ScrollToTop />
        <div className="App">
          <Switch>
            <Route exact path={['/', '']}>
              <Home LINKS={LINKS} />
            </Route>
            <Route path={'/games'}>
              <Platform addNetwork={addNetwork} />
            </Route>
            <Route path="/presale">
              <Presale addNetwork={addNetwork} />
            </Route>
          </Switch>
        </div>
      </Router>
    </AppContext.Provider>
  );
}

export default App;
