import { useState } from 'react';

import Header from '../Header/Header';
import { AppContext } from '../../App';

function ReferralSystem(props) {
  const { web3Account, address, onConnect, contract } = props;
  const [signed, setSigned] = useState('');

  // action=register
  // &address=0xD2c364B1Fde3B8Cf433e77F88c268b222d2d1769
  // &registerReferralCode=6b30f201f8f783804faa
  // &ts=1638192135896
  const signHandleClick = async () => {
    const exampleMessage = `action=register&address=${address}`;
    try {
      if (!web3Account) {
        return;
      }

      const from = address;
      const msg = `0x${Buffer.from(exampleMessage, 'utf8').toString('hex')}`;
      const sign = await window.ethereum.request({
        method: 'personal_sign',
        params: [msg, from, 'Example password'],
      });
      setSigned(sign);
      console.log('sign:', sign);
    } catch (err) {
      console.error(err);
    }
  };

  const addHandleClick = async () => {
    const result = await window.ethereum.request({
      method: 'wallet_watchAsset',
      params: {
        type: 'ERC20',
        options: {
          address: '0xa69BdaF45d1b965CA73dd08443480B33899A84D5',
          symbol: 'ARI',
          decimals: 0,
          image: '',
        },
      },
    });
    console.log('result', result);
  };

  return (
    <div className="referral" style={{ paddingInline: '48px' }}>
      <Header />
      <button onClick={signHandleClick}>sign something</button>
      <button onClick={addHandleClick}>add token</button>
      <p className="text font14">{signed}</p>
    </div>
  );
}

const ReferralSystemContainer = (props) => {
  return (
    <AppContext.Consumer>
      {(context) => (
        <ReferralSystem
          web3Account={context.web3Account}
          address={context.address}
          onConnect={context.onConnect}
          {...props}
        />
      )}
    </AppContext.Consumer>
  );
};

export default ReferralSystemContainer;
