import '../style/Home/home.css';
import { useState, useEffect } from 'react';
import LoaderScreen from '../Components/UI/LoaderScreen';

import Header from '../Components/Home/Header/Header';
import Hero from '../Components/Home/Hero/Hero';
import PlayGames from '../Components/Home/Games/PlayGames';
import Advantages from '../Components/Home/Advantages/Advantages';
import Investing from '../Components/Home/Investing/Investing';
import Roadmap from '../Components/Home/Roadmap/Roadmap';
import Footer from '../Components/Home/Footer/Footer';

function Home(props) {
  const { LINKS } = props;
  const [loading, setLoading] = useState(true);
  const [timeForLoad, setTimeForLoad] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setTimeForLoad(false);
    }, 1500);

    let lastScrollTop = 0;
    const navBar = document.getElementById('navbar');
    const screenHight = window.visualViewport.height;

    window.addEventListener('scroll', () => {
      let scrollTop = window.scrollY;
      if (scrollTop > 100) {
        navBar.classList.add('header_bg');
      } else {
        navBar.classList.remove('header_bg');
      }

      if (scrollTop > lastScrollTop && scrollTop > screenHight) {
        navBar.style.top = '-100%';
      } else {
        navBar.style.top = '0';
      }
      lastScrollTop = scrollTop;
    });
  }, [loading]);

  return (
    <div className="home">
      <LoaderScreen loading={loading} timeForLoad={timeForLoad} />
      <Header
        LINKS={LINKS}
        primaryButton={{
          title: 'Private sale',
          classes: 'header__button',
        }}
      />
      <Hero setLoading={setLoading} timeForLoad={timeForLoad} LINKS={LINKS} />
      {!loading && !timeForLoad ? (
        <div>
          <PlayGames />
          <Advantages />
          <Investing />
          <Roadmap />
          <Footer LINKS={LINKS} />
        </div>
      ) : null}
    </div>
  );
}

export default Home;
