import '../../../style/Platform/games/games_hero.css';

import { useEffect, useState } from 'react';
import BigNumber from 'bignumber.js';

import chest from '../../../img/Platform/Games/games_hero/chest.png';
import coin from '../../../img/Platform/Games/games_hero/coin.png';
import player from '../../../img/Platform/Games/games_hero/player.png';

function GamesHero(props) {
  const { getTotalRewards, getTotalPlayers } = props;
  const [prizePool, setPrizePool] = useState(null);
  const [playersOnline, setPlayersOnline] = useState(null);
  let isSubscribed = true;

  useEffect(() => {
    getData();

    return () => {
      isSubscribed = false;
    };
  }, []);

  const getData = async () => {
    const prizePoolWei = await getTotalRewards().call();
    const prizePool = numberFormating(prizePoolWei);
    const playersOnline = await getTotalPlayers().call();
    if (isSubscribed) {
      setPrizePool(prizePool);
      setPlayersOnline(playersOnline);
    }
  };

  const numberFormating = (num) => {
    const formated = BigNumber(num).dividedBy(BigNumber(10).pow(18)).toFixed();
    return new Intl.NumberFormat('de-DE').format(formated);
  };

  return (
    <div className="games__hero">
      <svg
        className="games__hero_bg_img"
        width="309"
        height="228"
        viewBox="0 0 309 228"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          opacity="0.25"
          d="M264.747 132.915C240.434 128.082 189.782 163.124 167.495 181.249C145.208 163.124 94.5552 128.081 70.242 132.914C58.0854 193.333 112.79 271.875 112.79 271.875C-57.4021 157.083 15.5374 0 15.5374 0C88.4769 12.0833 167.495 108.75 167.495 108.75C167.495 108.75 246.512 12.0833 319.452 0C319.452 0 355.922 48.3333 337.687 120.833C337.687 120.833 349.843 120.833 362 108.75C362 108.75 349.843 211.458 234.356 290C234.356 290 252.591 271.875 282.982 187.292C282.982 187.292 276.904 193.333 258.669 199.375C258.669 199.375 270.826 163.125 264.747 132.915Z"
          fill="#161623"
        />
      </svg>
      <div className="games__hero_content">
        <h2 className="games__hero_title title font_subtitle">
          Manarium Playground
        </h2>
        <p className="text font14 games__hero_text">
          Manarium is a unique blockchain gaming platform with play to earn
          games.
        </p>
        <div className="games__hero_info">
          {/* A Block */}
          <div className="games__hero_info_block">
            <img className="info_block_img" src={chest} />
            <div className="info_block_stats">
              <p className="title font14">Total Prize Pool</p>
              {prizePool ? (
                <h3 className="title font18">{prizePool} ARI</h3>
              ) : (
                <div className="info_block_stats_placeholder"></div>
              )}
            </div>
          </div>
          {/* A Block */}
          <div className="games__hero_info_block">
            <img className="info_block_img" src={coin} />
            <div className="info_block_stats">
              <p className="title font14">Ari Token Price</p>
              <h3 className="title font18">Comming soon...</h3>
            </div>
          </div>
          {/* A Block */}
          <div className="games__hero_info_block">
            <img className="info_block_img" src={player} />
            <div className="info_block_stats">
              <p className="title font14">Players On-line</p>
              {playersOnline ? (
                <h3 className="title font18">{playersOnline}</h3>
              ) : (
                <div className="info_block_stats_placeholder"></div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default GamesHero;
