import '../../../style/Platform/games/games_table.css';

import GameCard from './GameCard';
import PrimaryButton from '../../UI/PrimaryButton';
// *** IMAGES
import spiky_walls from '../../../img/Home/PlayGames/spikywalls.png';
import tothemoon from '../../../img/Home/PlayGames/tothemoon.png';
import reborn from '../../../img/Home/PlayGames/reborn.png';
import game from '../../../img/Platform/Games/games_table/game_preview.png';
// *** DEMOS
import trailer1 from '../../../media/trailer1.mp4';

const data = [
  {
    title: 'Spiky Walls',
    devs: 'Manarium',
    image: spiky_walls,
    demo: trailer1,
    color: '#008C41',
    gameLink: 'https://www.spikywalls.fun/',
    description:
      "Make the bird fly, but don't touch the spikes! Feed the bird to get points and avoid obstacles. How far can you make it?",
  },
  {
    title: 'To The Moon',
    devs: 'Manarium',
    image: tothemoon,
    demo: trailer1,
    color: '#7E00E2',
    gameLink: 'https://www.2themoon.fun/',
    description:
      'Launch your rocket as high into orbit as possible. Blast off for the stratosphere and try to reach the Moon.',
  },
  {
    title: 'Coming soon',
    devs: 'Manarium',
    image: reborn,
    demo: trailer1,
    color: '#E29500',
    gameLink: '',
    description: 'Comming soon...',
  },
];

function GamesTable(props) {
  const { getInfo } = props;

  return (
    <div className="games__table">
      <div className="games__table__content">
        {data.map((card, index) => {
          return (
            <GameCard
              key={index}
              title={card.title}
              devs={card.devs}
              image={card.image}
              demo={card.demo}
              color={card.color}
              description={card.description}
              method={getInfo(card.title)}
              gameLink={card.gameLink}
            />
          );
        })}
        <div
          className="platform_game_card"
          style={{
            display: 'flex',
            flexDirection: 'column',
          }}
        >
          <div
            style={{
              padding: 15,
              marginTop: 'auto',
              marginBottom: 0,
            }}
          >
            <h3
              className="title font24"
              style={{
                marginBottom: '2%',
              }}
            >
              There is no game yet
            </h3>
            <p
              className="title font14"
              style={{
                marginBottom: '18%',
              }}
            >
              Apply and launch your game on Manarium gaming platform and earn
              ARI tokens for that.
            </p>
            <PrimaryButton
              isDisabled={true}
              title={'Comming soon'}
              classes={'card_info_ablout_button'}
            />
          </div>
          <div
            style={{
              position: 'absolute',
              inset: '0',
              zIndex: '-1',
              transform: 'translateZ(0)',
            }}
          >
            <img className="game_card_back_img" src={game} alt="" />
            <div
              style={{
                background: `linear-gradient(180deg, rgba(227, 109, 0, 0) 0%, rgba(227, 109, 0, 1) 60%)`,
                transform: 'scale(1)',
              }}
              className="game_card_back_fade"
            ></div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default GamesTable;
