import '../../../style/Home/header/header.css';

import Logo from './Logo';
import HeaderContainer from '../../UI/HeaderContainer';
import NavBarDesctop from './NavBarDesctop';
import NavBarMobile from './NavBarMobile';
import PrimaryButton from '../../UI/PrimaryButton';
import { Link } from 'react-router-dom';

function Header(props) {
  const { primaryButton, LINKS } = props;
  const data = [
    {
      href: 'games',
      title: 'Games',
      className: 'item-dropdown',
    },
    {
      href: 'advantages',
      title: 'Advantages',
    },
    {
      href: 'investing',
      title: 'Investing',
    },
    {
      href: 'roadmap',
      title: 'Roadmap',
    },
    {
      href: '',
      title: 'Developers',
    },
    {
      href: 'https://manarium.gitbook.io/manarium/LzHH7vsGZQZtJ9k8cYL3/',
      target: '_blank',
      rel: 'noreferrer',
      title: 'Whitepaper',
    },
  ];

  return (
    <HeaderContainer>
      <Logo />
      <NavBarDesctop data={data} />
      <PresaleButton primaryButton={primaryButton} />
      <NavBarMobile LINKS={LINKS} data={data} />
    </HeaderContainer>
  );
}

const PresaleButton = ({ primaryButton }) => {
  return (
    <Link className="header__button_link" to="/presale">
      <PrimaryButton
        isDisabled={false}
        title={primaryButton.title}
        classes={primaryButton.classes}
      />
    </Link>
  );
};

export default Header;
