import DropDownMenu from './DropDownMenu';
import game1 from '../../../img/Home/Header/spikywalls.png';
import game2 from '../../../img/Home/Header/tothemoon.png';
import { Link } from 'react-scroll';

function NavBarDesctop(props) {
  const { data } = props;
  const dropdown_data = [
    {
      title: 'Spiky Walls',
      text: "Make the bird fly, but don't touch the spikes!",
      imageSrc: game1,
      imageAlt: 'Spiky walls game logo miniature',
      link: 'https://www.spikywalls.fun/',
    },
    {
      title: 'To The Moon',
      text: 'Launch your rocket as high into orbit as possible.',
      imageSrc: game2,
      imageAlt: 'To The Moon game logo miniature',
      link: 'https://www.2themoon.fun/',
    },
  ];

  return (
    <div className="header__navbar">
      <ul className="header__navbar-desktop">
        {data.map((item, index) => {
          return (
            <li key={index} className={`menu-item ${item.className}`}>
              {item.target ? (
                <a
                  href={item.href}
                  className="title"
                  target={item.target || ''}
                  rel={item.rel || ''}
                >
                  {item.title}
                </a>
              ) : (
                <Link
                  to={item.href}
                  smooth={true}
                  duration={1500}
                  className={`title ${
                    item.title === 'Developers' ? 'text' : ''
                  }`}
                  target={item.target || ''}
                  rel={item.rel || ''}
                >
                  {item.title}
                </Link>
              )}
            </li>
          );
        })}
        <DropDownMenu data={dropdown_data} />
      </ul>
    </div>
  );
}

export default NavBarDesctop;
