import firebase from 'firebase/compat/app';
import 'firebase/compat/auth';
import 'firebase/compat/firestore';

const authFirebase = () =>
  firebase
    .auth()
    .signInWithEmailAndPassword(
      'playground-server@gmail.com',
      '9mm5u;cA7kM5eN#P'
    );

async function fetchFirebase(gameName) {
  try {
    const SCORE_COLLECTION_NAME = gameName + '_prev';
    firebase.initializeApp({
      apiKey: 'AIzaSyBqZaJ2fN-YUyVbmTo2ccO5T0fhhi2_wUg',
      authDomain: 'playground-bsc.firebaseapp.com',
      projectId: 'playground-bsc',
    });
    await authFirebase();

    const respond = await firebase
      .firestore()
      .collection(SCORE_COLLECTION_NAME)
      .orderBy('score', 'desc')
      .get();

    const wallets = [];
    respond.forEach(function (doc) {
      const data = doc.data();
      const item = [data.score, data.wallet];
      wallets.push(item);
    });
    // wallets = [ [10, 0x00...], [5, 0x123...], [100, 0x666...], ... ];

    return wallets;
  } catch (e) {
    console.log(e);
  }
}

export default fetchFirebase;
